* {
  margin: 0;
  padding: 0;
}
.HomeScreen {
  position: relative;
}
.HomeScreen .overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(../images/codicon.png);
  background-size: cover;
  width: 25%;
  height: 250px;
}
.HomeScreen span {
  font-style: italic;
}
.HomeScreen .Home__toptext {
  position: absolute;
  font-size: 42px;
  color: white;
  font-weight: 800;
  top: 10%;
  left: 35%;
  letter-spacing: 3px;
  word-spacing: 2.2px;
  text-transform: capitalize;
  text-shadow: 2px 2px 2px black;
  animation: text 2s 1;
}
.HomeScreen .Home__bottomtext {
  position: absolute;
  font-size: 38px;
  color: blue;
  filter: blur(0.6px);
  /* display: none; */
  font-weight: 800;
  top: 18%;
  left: 42%;
  text-transform: uppercase;
  text-shadow: 2px 2px 2px black;
  animation: text2 3s 1;
}
@keyframes text {
  0% {
    visibility: hidden;
  }
  50% {
    visibility: hidden;
  }
  80% {
    visibility: visible;
  }
  100% {
    visibility: visible;
  }
}
@keyframes text2 {
  0% {
    visibility: hidden;
  }
  50% {
    visibility: hidden;
  }
  80% {
    visibility: hidden;
  }
  100% {
    display: block;
  }
}
