.footer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 100%;
  background: indigo;
  align-items: center;
}
.footer__nav {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom: 2px solid white;
}
.footer__navi {
  padding: 0 25px;
  font-size: 20px;
  color: white;
  font-weight: 300;
}
.footer__content {
  margin: 10px;
  padding: 4px 20px;
}
.footer__content p {
  font-size: 18px;
  align-items: center;
  color: white;
  word-spacing: 2px;
  width: 60%;
  margin: 4px auto;
}
.footer__socialmedia {
  padding: 20px;
  color: white;
}
.footer__copyrights {
  color: white;
  width: 100%;
  padding: 4px;
  text-align: center;
  display: block;
  background: #2b004b;
}
@media only screen and (max-width: 699px) {
  .footer__content p {
    width: 90%;
  }
}
